.card-lg{margin:20px 20px 0 20px;border:1px solid #595959;width:280px;color:#595959;border-radius:6px}.card-lg .card-lg-title{border-bottom:1px solid #595959;padding:15px;font-size:18px}.card-lg .card-lg-body{padding:15px}.card-lg .card-lg-body .li-style{font-weight:bold;list-style:none;margin-bottom:5px}.card-lg .card-lg-body .li-style::before{content:"•";padding-right:8px;color:#c5165b}.margin{display:block;margin-bottom:10px;margin-left:15px}#list{font-weight:normal}.btn-float{position:fixed;right:80px}@media(min-width: 768px){.card-lg{width:500px}}